<script>
import HeaderComponent from "@/components/Header1/header1.vue";
export default {
  name: "viewComponent",
  components: { HeaderComponent },
  data() {
    return {
      listData: {},
      detail: {},
      insurance: null,
      pdfs: null,
      people: [],
      workPeople: "",
      workTime: {},
      workType: [],
      aboutPeople: [],
    };
  },
  computed: {},
  methods: {
    // API取得資料
    async getEveryDayDetail(apply_Pk) {
      try {
        const res = await this.$store.dispatch("getEveryDayDetail", apply_Pk);
        if (res.data.isOK) {
          const data = res.data.data;
          this.detail = data.detail;
          if (this.detail.aboutPeople) {
            this.aboutPeople = this.detail.aboutPeople
              .map((e) => e.name)
              .join();
          }
          this.detail.begin_Work_Time = data.detail.begin_Time.split(" ")[1];
          this.detail.end_Work_Time = data.detail.end_Time.split(" ")[1];
          this.insurance = data.insurance;
          this.pdfs = data.pdfs;
          this.people = data.people || [];

          if ((this.detail.fab_Entrance_Id ?? "") !== "") {
            this.workPeople = data.people
              .map(
                (e) =>
                  `${e.p_Name}(${e.short_Name}/ ${
                    e.card_Id === null ? "無" : e.card_Id
                  })`
              )
              .join();
          } else {
            this.workPeople = data.people
              .map((e) => `${e.p_Name}(${e.short_Name})`)
              .join();
          }

          this.workTime = data.workTime;
          this.workType = data.workType.map((e) => e.wtn).join(", ");

          console.log(data);
        }
      } catch (err) {
        console.log(err);
      }
    },
  },
  mounted() {
    this.listData = JSON.parse(this.$route.query.data);
    this.getEveryDayDetail(this.listData.apply_Pk);
    this.$store.commit("HEADER_HANDLER", false);
    this.$store.commit("BREADCRUMB_HANDLER", false);
    this.$root.$children[0].sidebarSwitch = false;
  },
};
</script>

<template src="./view.html"></template>
